.editor {
  width: 100%;
}
.editor:after {
  content: '';
  clear: both;
  display: block;
  visibility: hidden;
  height: 0;
}
.col {
  float: left;
  width: 50%;
}

.full {
  width: 100%;
  height: 100%;
}
.wrap {
  position: relative;
  height: 100%;
  background: #eee;
  padding: 5px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.preview {
  overflow-y: scroll;
  height: 100%;
  height: 100vh;
  padding: 10px;
  border: 1px solid #999;
}


